    margin-left: 40px;
    font-size: 48px;
    color: white;
    font-family: "poppinsBold"
}

.home-social{
    position: absolute;
    display: flex;
    top: 48%;
    left: 0;
    margin-left: 40px;
}
.home-social > img {
    width: 20px;
    height: 20px;
