.home-scroll{
    position: absolute;
    top: 60%;
    left: 0;
    width: 24px;
    height: 40px;
    margin-left: 60px;
    -webkit-animation-name: scrollIndicatorAnimation;
            animation-name: scrollIndicatorAnimation;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out
}

@-webkit-keyframes scrollIndicatorAnimation{
    0%   {-webkit-transform: translate(0px,10px);transform: translate(0px,10px)}
    50%  {-webkit-transform: translate(0px,30px);transform: translate(0px,30px)}
    100% {-webkit-transform: translate(0px,10px);transform: translate(0px,10px)}
}

@keyframes scrollIndicatorAnimation{
    0%   {-webkit-transform: translate(0px,10px);transform: translate(0px,10px)}
    50%  {-webkit-transform: translate(0px,30px);transform: translate(0px,30px)}
    100% {-webkit-transform: translate(0px,10px);transform: translate(0px,10px)}
}

