    position: absolute;
    width: 0%;
    height: 50%;
    background: #F2F2F2;   
}

.animationDefaults {
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.overlayAnimation-in {
    -webkit-animation-name: overlayKeyframeIn;
            animation-name: overlayKeyframeIn;
}

.overlayAnimation-out {
    -webkit-animation-name: overlayKeyframeOut;
            animation-name: overlayKeyframeOut;
}


.titleAnimation-in{
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
    -webkit-animation-name: titleIn;
            animation-name: titleIn;
}

.titleAnimation-out{
    -webkit-animation-name: titleOut;
            animation-name: titleOut;
}

.showAnimation-in{
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    -webkit-animation-name: showIn;
            animation-name: showIn;
}

.showAnimation-out{
    -webkit-animation-name: showOut;
            animation-name: showOut;
}

@-webkit-keyframes titleIn {
    from{
        -webkit-transform: translate(0px,-20px);
                transform: translate(0px,-20px);
        opacity:0.0;
    }
    to{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
}

@keyframes titleIn {
    from{
        -webkit-transform: translate(0px,-20px);
                transform: translate(0px,-20px);
        opacity:0.0;
    }
    to{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
}

@-webkit-keyframes titleOut {
    from{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
    to{
        -webkit-transform: translate(0px,-20px);
                transform: translate(0px,-20px);
        opacity:0.0;
    }
}

@keyframes titleOut {
    from{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
    to{
        -webkit-transform: translate(0px,-20px);
                transform: translate(0px,-20px);
        opacity:0.0;
    }
}

@-webkit-keyframes showIn {
    from{
        -webkit-transform: translate(0px,20px);
                transform: translate(0px,20px);
        opacity:0.0;
    }
    to{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
}

@keyframes showIn {
    from{
        -webkit-transform: translate(0px,20px);
                transform: translate(0px,20px);
        opacity:0.0;
    }
    to{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
}

@-webkit-keyframes showOut{
    from{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
    to{
        -webkit-transform: translate(0px,20px);
                transform: translate(0px,20px);
        opacity:0.0;
    }
}

@keyframes showOut{
    from{
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
        opacity:1.0;
    }
    to{
        -webkit-transform: translate(0px,20px);
                transform: translate(0px,20px);
        opacity:0.0;
    }
}

@-webkit-keyframes overlayKeyframeIn{
    from {
        width: 0%;
    }
    to{
        width: 100%;
    }
}

@keyframes overlayKeyframeIn{
    from {
        width: 0%;
    }
    to{
        width: 100%;
    }
}

@-webkit-keyframes overlayKeyframeOut{
    from {
        width: 100%;
    }
    to{
        width: 0%;
    }
}

@keyframes overlayKeyframeOut{
    from {
        width: 100%;
    }
    to{
        width: 0%;
    }
}

